import React from "react";
import { useHistory } from "react-router-dom";

import "./Login.scss";
import TelegramLoginButton from "react-telegram-login";

const Login = () => {
  const history = useHistory();

  const handleTelegramResponse = (response) => {
    localStorage.setItem("tgBotValid", true);
    history.push(`/covid19/dashboard`);
    if (response) {
      localStorage.setItem("tgLoginStatus", true);
    } else {
      localStorage.setItem("tgLoginStatus", false);
    }
  };

  localStorage.setItem("tgBotValid", false);
  return (
    <TelegramLoginButton
      dataOnauth={handleTelegramResponse}
      botName="covid19LoginBot"
      className="login"
    />
  );
};

export default Login;
