import React from "react";
import { useSelector } from "react-redux";
import "./Chart.scss";
import { Line } from "react-chartjs-2";

const Chart = () => {
  const stats = useSelector((state) => state.countryStats);
  if (!stats) return <div>Chart</div>;
  const { activeArr, confirmedArr, recoveredArr, deathArr, dateArr } = stats;
  return (
    <div className="chart">
      <Line
        data={{
          labels: dateArr,
          datasets: [
            {
              label: "Cases",
              data: confirmedArr,
              backgroundColor: "rgba(182, 255, 99, 0.2)",
              borderColor: "rgba(182, 255, 99, 1)",
              borderWidth: 1,
            },
            {
              label: "Deathes",
              data: deathArr,

              backgroundColor: "rgba(255, 99, 99, 0.2)",
              borderColor: "rgba(255, 99, 99, 1)",
              borderWidth: 1,
            },

            {
              label: "Recovered",
              data: recoveredArr,

              backgroundColor: "rgba(99, 255, 213, 0.2)",
              borderColor: "rgba(99, 255, 213, 1)",
              borderWidth: 1,
            },
          ],
        }}
        height={10}
        width={20}
        options={{
          maintainAspectRation: false,
        }}
      ></Line>
    </div>
  );
};

export default Chart;
