import React from "react";
import Dashboard from "./components/Dashboard/Dashoboard";
import Countries from "./components/CountriesSelect/CountriesSelect";
import Country from "./components/Country/Country";
import Login from "./components/Login/Login";
import "./App.scss";
import { Route, Switch, BrowserRouter as Router } from "react-router-dom";

const App = () => {
  return (
    <>
      <Router>
        <Switch>
          <Route path="/covid19/" exact>
            <Login />
          </Route>
          <Route path="/covid19/dashboard" exact>
            <Dashboard />
          </Route>
          <Route path="/covid19/countries">
            <Countries />
          </Route>
          <Route path="/covid19/country/:Country">
            <Country />
          </Route>
        </Switch>
      </Router>
    </>
  );
};

export default App;
