import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";

import { getSummary } from "../../service/service";
import CountriesSelect from "../CountriesSelect/CountriesSelect";
import "./Dashboard.scss";

const Dashboard = () => {
  interface RootState {
    countriesData: any;
  }
  const [globalData, setGlobalData] = useState<any[]>();

  const countriesData = useSelector((state: RootState) => state.countriesData);

  useEffect(() => {
    getSummary().then((data: any) => {
      setGlobalData(data);
    });
  }, []);

  if (!globalData) return <div>Loading...</div>;
  const gridList = globalData.map((item: any) => {
    const { title, count } = item;
    return (
      <div className="grid" key={title}>
        <h2>{title}</h2>
        <p>{count}</p>
      </div>
    );
  });

  return (
    <div className="dashboard">
      <div className="container">
        <div className="table">{gridList}</div>
        <CountriesSelect />
      </div>
    </div>
  );
};

export default Dashboard;
