import { summary } from "../api/summary";
import { stats } from "../api/stats";
const url: string = "https://api.covid19api.com/";

const getResource = async (url: string) => {
  const res = await fetch(url);
  if (!res.ok) {
    // throw new Error(`Could not fetch ${url} +, reeived ${res.status}`);
    return null;
  }
  return await res.json();
};

export const getSummary = async () => {
  // const configUrl = url + "summary";
  const res = summary;

  return transformSummaryData(res);
};
export const getCountries = async () => {
  const configUrl = url + "summary";
  const res = await getResource(configUrl);

  return transformCountriesData(res);
};

export const getCountryStatsFromDay = async (countryName: string) => {
  const fromDay = "2020-03-21T13:13:30Z";
  const configUrl = `${url}live/country/${countryName}/status/confirmed`;
  const res = await getResource(configUrl);

  return transformCountryStatsFromDay(res);
};

const transformSummaryData = (data: any) => {
  const { ID, ...global } = data.Global;
  const arr = [];
  for (let key in global) {
    arr.push({
      title: key,
      count: global[key],
    });
  }
  return arr;
};

const transformCountriesData = (data: any) => {
  const { ID, ...countries } = data.Countries;
  const arr = [];
  let max = 150,
    count = 0;

  for (let key in countries) {
    count++;
    const { ID, ...datas } = countries[key];

    if (count < max) {
      arr.push({
        ...datas,
      });
    }
  }
  return arr;
};
const transformCountryStatsFromDay = (data: any) => {
  const activeArr = [],
    confirmedArr = [],
    recoveredArr = [],
    deathArr = [],
    dateArr = [];
  for (let i = 0; i < data.length; i++) {
    const { Active, Confirmed, Recovered, Deaths, Date } = data[i];
    const configDate = Date.slice(0, 10);

    activeArr.push(Active);
    confirmedArr.push(Confirmed);
    recoveredArr.push(Recovered);
    deathArr.push(Deaths);
    dateArr.push(configDate);
  }
  return {
    activeArr,
    confirmedArr,
    recoveredArr,
    deathArr,
    dateArr,
  };
};
