import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getCountryStatsFromDay } from "../../service/service";
import { setCountryStats } from "../../redux/actions";
import Chart from "../Chart/Chart";

interface RootState {
  countryData: any;
  countryStats: any;
}
const Country = () => {
  const dispatch = useDispatch();

  const localStorageData: any = localStorage.getItem("countryData");
  const country =
    useSelector((state: RootState) => state.countryData) ||
    JSON.parse(localStorageData);

  useEffect(() => {
    const countryName: string = country.Slug;

    getCountryStatsFromDay(countryName).then((data) => {
      dispatch(setCountryStats(data));
    });
  }, []);

  if (!country) return <div className="">Loading...</div>;

  return (
    <div className="country">
      <div className="container">
        <h1 className="country-title">{country.Country}</h1>
        <Chart />
      </div>
    </div>
  );
};

export default Country;
